    body {
        background: url('https://www.pixelstalk.net/wp-content/uploads/images6/Fitness-Desktop-Wallpaper.jpg') no-repeat center center fixed;
        background-size: cover;
        font-size: 1.1rem;
        font-weight: 400;
        line-height: 1.5;
    }
    
    .container {
        background: black;
        opacity: 0.8;
        border: 0.2rem solid black;
        border-radius: 1rem;
        padding: 2rem;
        margin-bottom: -2rem;
        margin-right: 8rem;
        margin-left: 1rem;
        margin-top: 3rem;
        overflow-y: auto;
        overflow-x: hidden;
        padding-bottom: 2.8rem;
        padding-top: 1.6rem;
        width: 130rem;
        color: white;
    }
    
    @media screen and (max-width: 1024px) {
        .container {
            background: black;
            opacity: 0.8;
            border: 0.2rem solid black;
            border-radius: 1rem;
            padding: 2rem;
            margin-bottom: -2rem;
            margin-right: 8rem;
            margin-left: 1rem;
            margin-top: 3rem;
            overflow-y: auto;
            overflow-x: hidden;
            padding-bottom: 2.8rem;
            padding-top: 1.6rem;
            width: 40rem;
            color: white;
        }
    }
    /* Below 560px  */
    
    @media (max-width: 35em) {
        .container {
            background: black;
            opacity: 0.8;
            border: 0.2rem solid black;
            border-radius: 1rem;
            padding: 2rem;
            margin-bottom: -2rem;
            margin-right: 8rem;
            margin-left: 0.3rem;
            margin-top: 3rem;
            overflow-y: auto;
            overflow-x: hidden;
            padding-bottom: 2.8rem;
            padding-top: 1.6rem;
            width: 23rem;
            color: white;
        }
    }